import React, { Component } from "react";
import { connect } from "react-redux";
class PostForm extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    const Image = this.getImage.value;
    const FirstName = this.getFirstName.value;
    const LastName = this.getLastName.value;
    const StreetName = this.getStreetName.value;
    const StreetNum = this.getStreetNum.value;
    const PhoneNum = this.getPhoneNum.value;
    const Gpa = this.getGpa.value;
    const data = {
      id: new Date(),
      Image,
      FirstName,
      LastName,
      StreetName,
      StreetNum,
      PhoneNum,
      Gpa,
      editing: false,
    };
    this.props.dispatch({
      type: "ADD_POST",
      data,
    });
    this.getImage.value = "";
    this.getFirstName.value = "";
    this.getLastName.value = "";
    this.getStreetName.value = "";
    this.getStreetNum.value = "";
    this.getPhoneNum.value = "";
    this.getGpa.value = "";
  };
  render() {
    return (
      <div className='entryForm'>
        <h1>Create Entry</h1>
        <p>
          Copy this URL into the URL field
          https://i.pinimg.com/originals/ba/d5/b8/bad5b88b368767b023c43f95dbd163c8.png
        </p>
        <form onSubmit={this.handleSubmit}>
          <input
            required
            type='text'
            ref={(input) => (this.getImage = input)}
            placeholder='Enter Image URL'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getFirstName = input)}
            placeholder='Enter First Name'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getLastName = input)}
            placeholder='Enter Last Name'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getStreetName = input)}
            placeholder='Enter Street Name'
          />
          <input
            required
            type='num'
            ref={(input) => (this.getStreetNum = input)}
            placeholder='Enter Street Number'
          />
          <input
            required
            type='tel'
            ref={(input) => (this.getPhoneNum = input)}
            placeholder='Enter Phone Number'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getGpa = input)}
            placeholder='Enter GPA'
          />
          <button>Save</button>
        </form>
      </div>
    );
  }
}
export default connect()(PostForm);
