import React, { Component } from "react";
import { connect } from "react-redux";
class Post extends Component {
  render() {
    return (
      <div className='Result'>
        <span>
          <img src={this.props.post.Image} alt='' title='' />
        </span>
        <br />
        <span>
          {this.props.post.FirstName} {this.props.post.LastName}
        </span>

        <br />
        <span>
          {this.props.post.StreetName} {this.props.post.StreetNum}
        </span>
        <br />
        <span>{this.props.post.PhoneNum}</span>
        <br />
        <span>GPA {this.props.post.Gpa}</span>
        <br />
        <button
          onClick={() =>
            this.props.dispatch({ type: "EDIT_POST", id: this.props.post.id })
          }
        >
          Edit
        </button>
        <button
          onClick={() =>
            this.props.dispatch({ type: "DELETE_POST", id: this.props.post.id })
          }
        >
          Delete
        </button>
      </div>
    );
  }
}
export default connect()(Post);
