import React, { Component } from "react";
import { connect } from "react-redux";
class EditComponent extends Component {
  handleEdit = (e) => {
    e.preventDefault();
    const newImage = this.getImage.value;
    const newFirstName = this.getFirstName.value;
    const newLastName = this.getLastName.value;
    const newStreetName = this.getStreetName.value;
    const newStreetNum = this.getStreetNum.value;
    const newPhoneNum = this.getPhoneNum.value;
    const newGpa = this.getGpa.value;
    const data = {
      id: new Date(),
      newImage,
      newFirstName,
      newLastName,
      newStreetName,
      newStreetNum,
      newPhoneNum,
      newGpa,
    };
    this.props.dispatch({ type: "UPDATE", id: this.props.post.id, data: data });
  };
  render() {
    return (
      <div>
        <h1>Create Entry</h1>
        <form className='editForm' onSubmit={this.handleEdit}>
          <input
            required
            type='text'
            ref={(input) => (this.getImage = input)}
            defaultValue={this.props.post.Image}
            placeholder='Enter Image URL'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getFirstName = input)}
            defaultValue={this.props.post.FirstName}
            placeholder='Enter First Name'
          />

          <input
            required
            type='text'
            ref={(input) => (this.getLastName = input)}
            defaultValue={this.props.post.LastName}
            placeholder='Enter Last Name'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getStreetName = input)}
            defaultValue={this.props.post.StreetName}
            placeholder='Enter Street Name'
          />
          <input
            required
            type='num'
            ref={(input) => (this.getStreetNum = input)}
            defaultValue={this.props.post.StreetNum}
            placeholder='Enter Street Number'
          />
          <input
            required
            type='tel'
            ref={(input) => (this.getPhoneNum = input)}
            defaultValue={this.props.post.PhoneNum}
            placeholder='Enter Phone Number'
          />
          <input
            required
            type='text'
            ref={(input) => (this.getGpa = input)}
            defaultValue={this.props.post.Gpa}
            placeholder='Enter GPA'
          />
          <br />
          <button>Update</button>
        </form>
      </div>
    );
  }
}
export default connect()(EditComponent);
